import styled from 'styled-components';
import { useState } from 'react';
import { LuBellOff } from "react-icons/lu";
import { ToggleIcon } from '@/components/common/svg/ToggleSVG';

function SubscribeToggle() {
  const [ toggle, setToggle ] = useState(false)

  const toggleSubscribe = () => {
    setToggle(!toggle);
  }

  return (
    <SubscribeToggleStyled onClick={toggleSubscribe}>
      {toggle ? (
          <div className="subscribe-on">
            <img src="/icons/subs_on.svg" alt="subscribeOn" />
            <p>구독 진행중</p>
          </div>
        )
        :
        (
          <div className="subscribe-off">
            <img src="/icons/subs_off.svg" alt="subscribeOff" />
            <p>구독 일시정지 중</p>
          </div>
        )
      }
      <div className="not-subscribe">
        <LuBellOff />
        <p>구독 상태가 아닙니다.</p>
      </div>
    </SubscribeToggleStyled>
  );
}

const SubscribeToggleStyled = styled.div`
    .subscribe-on, .subscribe-off, .not-subscribe {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 0.725rem;
        padding: 1.25rem;
        border-radius: ${({theme}) => theme.borderRadius.medium};
        height: 3.875rem;
        width: 14rem;
        font-weight: ${({theme}) => theme.fontWeight.medium};

        img {
            height: 2rem;
            width: 3.25rem;
        }
    }

    .subscribe-on {
        border: 1px solid ${({theme}) => theme.color.primary};
        color: ${({theme}) => theme.color.primary};
        background-color: ${({theme}) => theme.color.colorBackground};
    }

    .subscribe-off {
        border: 1px solid ${({theme}) => theme.color.line};
        color: ${({theme}) => theme.color.text};
        background-color: ${({theme}) => theme.color.surface};
    }

    .not-subscribe {
        border: 1px solid ${({theme}) => theme.color.line};
        color: ${({theme}) => theme.color.text};
        background-color: ${({theme}) => theme.color.surface};
        svg {
            width: 1.25rem;
            height: 1.25rem;
            color: ${({theme}) => theme.color.warning};
        }
    }


`;

export default SubscribeToggle;